.about {
  padding: 70px 0;
  h2 {
    margin-bottom: 35px;
    @include r($tablet) {
      margin-top: 40px;
      margin-bottom: 15px; } }
  p {
    width: 92%;
    @include r($mobile) {
      width: 100%; } }
  a {
    margin-top: 50px;
    left: 0;
    transform: translateX(0);
    @include r($mobile) {
      left: 50%;
      transform: translateX(-50%); } }
  &__image {
    width: 570px;
    height: 845px;
    float: right;
    box-shadow: 20px 20px 0 0 $primary;
    margin-top: -150px;
    @include r($tablet) {
      width: 650px;
      height: 420px;
      overflow: hidden;
      position: relative;
      left: 50%;
      transform: translateX(-50%);
      float: none; }
    @include r($mobile) {
      width: 345px;
      height: 280px;
      box-shadow: 5px 5px 0 0 $primary; }
    img {
      width: 100%;
      display: block;
      @include r($tablet) {
        position: relative;
        top: -270px; }
      @include r($mobile) {
        top: -100px; } } }
  .container {
    @include r($tablet) {
      padding: 0 60px; }
    @include r($mobile) {
      padding: 0 15px; } } }

