.intro {
  background: #0E151F url('../img/background/bg-01.jpg');
  @include cover;
  height: 720px;
  color: $white;
  @include r($mobile) {
    height: 620px; }
  h1 {
    font-size: 62px;
    font-weight: 900;
    font-family: 'Museo Sans';
    line-height: 1.34;
    margin-bottom: 20px;
    margin-top: 20px;
    @include r($tablet) {
      font-size: 48px;
      margin-top: 40px; }
    @include r($mobile) {
      font-size: 36px;
      br {
        display: none; } } }
  p {
    font-size: 20px;
    line-height: 1.5;
    margin-bottom: 50px;
    color: $white;
    @include r($tablet) {
      font-size: 18px; }
    @include r($mobile) {
      font-size: 16px;
      margin-bottom: 30px;
      br {
        display: none; } } } }
