.form {
  background-image: url('../img/background/bg-05.jpg');
  @include cover;
  padding: 100px 0;
  @include r($tablet) {
    padding-top: 70px;
    padding-bottom: 0;
    .container {
      padding: 0 60px; } }
  @include r($mobile) {
    padding-top: 40px;
    .container {
      padding: 0 15px; } }
  h2, p {
    color: $white;
    @include r($tablet) {
      text-align: center; } }
  p {
    font-size: 20px;
    @include r($mobile) {
      br {
        display: none; } } }
  h2 {
    margin-bottom: 15px; }
  &__social {
    margin-top: 50px;
    @include r($tablet) {
      display: flex;
      justify-content: center;
      margin-top: 35px;
      span {
        display: none; } }
    @include r($tablet) {
      margin-top: 40px; }
    @include r($mobile) {
      margin-top: 25px; }
    a {
      text-decoration: none;
      display: flex;
      align-items: center;
      color: rgba($white, 0.85);
      margin-bottom: 25px;
      @include r($tablet) {
        margin-bottom: 40px; }
      @include tr;
      i {
        @include center;
        background: $white;
        width: 55px;
        height: 55px;
        border-radius: 50%;
        color: $primary;
        margin-right: 15px; }
      &:hover {
        text-decoration: underline;
        color: $white; } } }
  form {
    background-color: $white;
    box-shadow: 0 4px 32px 0 rgba(0, 0, 0, 0.12);
    text-align: center;
    padding: 35px 40px;
    margin-top: -170px;
    box-sizing: border-box;
    float: right;
    @include r($tablet) {
      margin-top: 0;
      width: 100%; }
    @include r($mobile) {
      padding: 25px 30px; }
    h3 {
      color: $secondary;
      margin-bottom: 10px;
      font-size: 40px;
      font-family: 'Museo Sans';
      font-weight: 900;
      @include r($mobile) {
        font-size: 28px; } }
    p {
      color: $body-text;
      font-size: 18px;
      @include r($mobile) {
        font-size: 16px; } } } }

