.capabilities {
  padding: 300px 0 100px;
  position: relative;
  @include r($mobile) {
    padding: 40px 0; }
  &:before {
    content: '';
    background-image: url('../img/capabilities/bg-circle.svg');
    width: 700px;
    height: 700px;
    @include cover;
    display: block;
    position: absolute;
    top: 50%;
    transform: translate(-57%, -50%); }
  h2 {
    margin-bottom: 70px;
    @include r($tablet) {
      margin-bottom: 48px; }
    @include r($mobile) {
      margin-bottom: 30px; }
    span {
      color: $primary; } }
  &__block {
    background-color: $white;
    box-shadow: 0 4px 32px 0 rgba(0, 0, 0, 0.12);
    position: relative;
    z-index: 10;
    @include r($tablet) {
      display: flex;
      height: 100%; }
    @include r($mobile) {
      flex-direction: column; }
    &__margin {
      @include r($tablet) {
        margin-bottom: 25px; } }
    &__image {
      height: 230px;
      overflow: hidden;
      @include r($tablet) {
        width: 50%;
        height: 100%;
        @include cover; }
      @include r($mobile) {
        width: 100%;
        height: 230px; } }
    &__content {
      padding: 25px;
      @include r($tablet) {
        width: 50%;
        display: flex;
        justify-content: center;
        flex-direction: column; }
      @include r($mobile) {
        width: 100%; }
      h3 {
        font-size: 20px;
        font-family: 'Museo Sans';
        line-height: 1.4;
        color: $secondary;
        margin-bottom: 15px;
        @include r($tablet) {
          text-align: left; } }
      ul {
        padding-left: 20px;
        li {
          font-size: 14px;
          line-height: 1.65;
          margin-bottom: 20px;
          position: relative;
          color: $body-text;
          &:before {
            content: '';
            width: 12px;
            height: 12px;
            background: $primary;
            border-radius: 50%;
            display: block;
            position: absolute;
            top: 5px;
            left: -20px; }
          &:last-child {
            @include r($tablet) {
              margin-bottom: 0; } } } } } } }
