.contact {
  padding-top: 50px;
  padding-bottom: 100px;
  background: $gray-02;
  @include r($tablet) {
    padding-bottom: 60px;
    padding-top: 30px; }
  h1 {
    font-size: 48px;
    font-family: 'Museo Sans';
    font-weight: 900;
    @include r($tablet) {
      margin-bottom: 20px; }
    @include r($mobile) {
      font-size: 32px; } }
  .form__social {
    margin-bottom: 40px;
    @include r($tablet) {
      justify-content: flex-start;
      display: block; }
    a {
      color: $secondary;
      text-decoration: none;
      &:hover {
        span {
          text-decoration: underline; } } }
    i {
      text-decoration: none; }
    span {
      @include r($tablet) {
        display: block; } } }
  iframe {
    box-shadow: 16px 16px 0 0 $primary;
    border: solid 8px $white !important;
    @include r($tablet) {
      width: 730px;
      height: 430px;
      box-shadow: 10px 10px 0 0 $primary;
      border: solid 8px $white; }
    @include r($mobile) {
      width: 345px;
      height: 240px;
      box-shadow: 5px 5px 0 0 $primary;
      border: solid 5px $white; } } }

